.container {
    width: 80%;
    height: 600px;
    background-color: #F4F4F4;
    border-radius: 40px;
    display: flex;
}

.container .left {
    width: 75%;
    padding: 20px 80px;
}

.container .left .header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 450px;
}

.container .left .header .date {
    font-size: 20px;
    font-weight: 500;
}

.container .left .header .inp-ctrl {
    width: 200px;
    height: 30px;
    position: relative;
    cursor: pointer;
}

.container .left .header .inp-ctrl:focus {
    cursor: text;
}

.container .left .header .inp-ctrl input {
    all: unset;
    width: 100%;
    height: 30px;
    padding: 2px 5px;
    font-size: 22px;   
    font-weight: 500;
}

.container .left .header .inp-ctrl .edit {
    all: unset;
    position: absolute;
    right: 20px;
    top: 10px;
    font-size: 22px;
}

.container .right {
    float: right;
    width: 25%;
    height: 100%;
    background-color: #ebebeb;
    border-top-right-radius: 40px;
    border-bottom-right-radius: 40px;
    display: flex;
    flex-direction: column;
    padding: 30px 20px;
    align-items: center;
    gap: 20px;
}

.container .right .greeting {
    font-size: 26px;
    font-weight: 400;
}

.time {
    font-size: 24px;
    font-weight: 400;
}

.left .temp {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 400px;
}

.left .temp-deg {
    font-size: 10em;
    color: #5f5f5f;
    margin-left: 50px;
}

.left .temp .desc {
    font-weight: 400;
    font-size: 2.2em;
}

.left .content {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
    gap: 60px;
    font-size: 22px;
}

.feel {
    width: 100%;
    text-align: center;
    margin-top: 20px;
    font-size: 22px;
}

.right .other {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 40px;
    font-size: 22px;
    margin-top: 50px;
}

.right .other p {
    display: flex;
    align-items: center;
    gap: 5px;
}